@mixin mq($minwidth) {
    @media screen and (min-width: $minwidth) {
        @content; } }

@mixin mqx($maxwidth) {
    @media screen and (max-width: $maxwidth) {
        @content; } }

%clearfix {
    &:after {
        content: "";
        display: table;
        clear: both; } }

%vertical-rhythm {
    margin-bottom: $spacing-unit / 2; }
